.contact{
    height: 100vh;
    background-color: #eb4974;
    display: flex;
    justify-content: center;
    align-items: center;

}

.container{
    height: 70%;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

}

.sendForm{
    width: 50%;
}
.sendForm form{
    display: flex;
    flex-direction: column;
}
form input{
    margin-bottom: 10px;
}
.button{
    width: 15%;
    height: 10%;
}
.button button{
    width: 100%;
    height: 100%;
}
@media screen and (max-width: 600px){
    .contact{
        height: 100vh;
        background-color: #eb4974;
        display: flex;
        justify-content: center;
        align-items: center;

    }

    .container{
        height: 90%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;

    }

    .sendForm{
        width: 100%;
    }
    .sendForm form{
        display: flex;
        flex-direction: column;
    }
    form input{
        margin-bottom: 10px;
    }
    .button{
        width: 35%;
        height: 10%;
    }
    .button button{
        width: 100%;
        height: 100%;
    }
}

